.offer {
	&__title {
		margin-top: 30px;
		margin-bottom: 30px;
		text-align: center;
		font-family: 'Cuprum', sans-serif; }
	&__text {
		line-height: 1.4; }
	&__image {
		display: flex;
		justify-content: space-between;
		margin-top: 40px;
		margin-bottom: 40px; }
	&-block {
		display: flex;
		flex-basis: 50%;
		margin-top: 40px; }
	&__card {
		background: white;
		padding: 30px;
		margin-right: 15px;
		box-shadow: 0 0 10px rgba(0,0,0,0.5);
		&:hover {
			box-shadow: 0 0 10px rgba(0,0,0,0.7); } } }
.mrn {
	margin-top: 10px; }

